<template>
  <div>
    <div style="text-align:left">
    <b style="text-align:left;color:#767783">基础配置</b>
    </div>
    <hr align="left" style="border:3 double #987cb9" width="80%" color=#E3C9D8 SIZE=2>
    <div style="text-align:center">
    <!-- <input type="button" @click="gets()" value="点我异步获取数据(Get)" /> -->
    <span id="p1"> {{status}}</span>
    <el-form>
      <el-form-item label="服务器IP:">
    <el-transfer
    style="text-align: left; display: inline-block"
    filterable
    filter-placeholder="请输入ip"
    v-model="value"
    :data="data">
  </el-transfer>
  </el-form-item>
  </el-form>
  <!-- <p v-for="item in value" :key="item">
    {{item}}
  </p> -->
  </div>
  <div style="text-align:left">
    <b style="text-align:left;color:#767783">任务配置</b>
  </div>
<hr align="left" style="border:3 double #987cb9" width="80%" color=#E3C9D8 SIZE=2>
<div  style="text-align:left">
<el-checkbox v-model="checkedPro" @change="checkedAllFun">进程定时扫描</el-checkbox> <br>
<el-checkbox v-model="checkedFile" @change="checkedFreshPage">文件异常定时扫描</el-checkbox> <br>
<el-checkbox v-model="checkedout" @change="checkedOutnetwork">外网访问定时扫描</el-checkbox>

</div>
<div style="text-align:left;margin-top:10px;">

  <strong style="text-align:left;color:#767783">定时配置</strong>
</div>
<hr align="left" style="border:3 double #987cb9" width="80%" color=#E3C9D8 SIZE=2>
 <div class="demo-input-suffix">   
  <el-form ref='form' :model="form" label-width="80px" label-position='left'	>
    <el-form-item label="任务Cron:">
   <el-popover v-model="cronPopover">
        <cron @change="changeCron" @close="cronPopover=false" i18n="cn"/>     
        <el-input slot="reference" v-model="execTime" placeholder="定时策略" @click="cronPopover=true" style="width: 30% ;margin-left: -70%"/>     
    </el-popover>
    </el-form-item>
    <el-form-item label="状态" style="text-align:left">
      <el-switch
    class="switch"
    :width='60'
    v-model="status"
    :active-value="0"
    :inactive-value="1"
    active-text="开启"
    inactive-text="关闭"
></el-switch>
    </el-form-item>
  <el-form-item label="备注:">
   <el-input
  type="textarea"
  :rows="2"
  placeholder="请输入内容"
  v-model="textarea" style="width: 30% ;margin-left: -70%">
</el-input>
  </el-form-item>
  </el-form>
 </div>
<el-row>
  <el-button type="success" @click="getbutton()" >提交</el-button>
</el-row>
  </div>
</template>

<style>
.switch .el-switch__label {
    position: absolute;
    color: #fff !important;
    z-index: 1;
    display: none;
}
.switch .is-active{
    display: block;
}
.switch .el-switch__label--left span{
    margin-left: 20px;
}

</style>

<script>
// var p1 = document.getElementById("p1");
import axios from "axios";
import {cron} from 'vue-cron'
// import {cronComponent} from 'vue-cron';
export default {
  components: { cron },
  name: "HelloWorld",
  data() {
      return {
        //穿梭框使用的值
        data: [],
        value: [],
        //复选框使用的值
        checkedFile: '',
        checkedPro: '',
        checkedout: '',
        checklist: [],
        //任务计划使用
        execTime:'',
        cronPopover:false,
        //开关
        status: true,
        form:{},
        //备注数据
        textarea: ''
      };
   
  },
  created(){//页面渲染之前执行，调用定义的方法
                //3.调用定义的方法
                this.gets()
            },
  methods: {
    changeCron(val){
            this.execTime = val
        },
    gets: function () {
      var parms = "";
      axios
        .get("http://127.0.0.1:8092/getaxios", parms)
        .then((response) => {
          // window.alert(response.data);
          this.data = response.data;
          // this.data2 =response.data;
          console.log(response);
        })
        .catch(function (response) {
          alert("请求失败");
           console.log(response);
        });
    },
    posts: function (i) {
      // 定义传参值
      const params = new URLSearchParams();
      params.append('serverip', this.value);
      params.append('taskoptions', i);
      params.append('cron',this.execTime);
      params.append('status',this.status);
      params.append('remarks',this.textarea);
      axios
        .post("http://127.0.0.1:8092/postaxios", params)
        .then((response) => {
          console.log(response)//成功
        })
        .catch(function (response) {
          alert("请求失败");
           console.log(response);
        });
    },
   
// 全选
checkedAllFun: function(e,checkedPro){
  console.log("进程"+this.checkedPro);
},
// 刷新
checkedFreshPage: function(e,checkedFile){
  console.log("文件"+this.checkedFile);
},
checkedOutnetwork: function(e,checkedout){
  console.log(this.checkedout);
},
getbutton: function (){
  // console.log(this.value);

this.checklist = [this.checkedPro,this.checkedFile,this.checkedout]
for (let i=0; i<this.checklist.length; i++)
{
    if (this.checklist[i]){
      if (i == 0){
        this.posts(i)
      console.log("进程定时扫描");
      }
      else if (i == 1){
      console.log("文件定时扫描");
      this.posts(i)
      }
      else if (i == 2){
      console.log("外网访问定时扫描");
      this.posts(i)
      }
      else{
      console.log("报错");
      }
    }
}
}

  },
};
</script>


